//
// Add line illustration :after
//

@mixin line-after(){
	display: flex;
    align-items: center;

	&::after{
        content: '';
        mask-image: url('data:image/svg+xml,<svg width="60" height="4" viewBox="0 0 60 4" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0.00464317 2.22845C0.270295 -0.0105155 17.2205 1.29019 20.8923 1.29019C52.6013 1.29019 60 -0.487744 60 1.29019C60 3.06812 39.699 3.06815 25.3016 3.06815C10.9043 3.06815 -0.261009 4.46742 0.00464317 2.22845Z" fill="%23B593D4"/></svg>');
        mask-repeat: no-repeat;
        mask-position: center;
        mask-size: 100%;
        margin-left: calc($spacing-small / 2);
        background-color: currentColor;
        max-width: 10%;
        min-width: 60px;
        height: 10px;
    }
}

//
// Add line illustration :after
//

@mixin line-under(){
	@include line-after();

	&:after{
		position: absolute;
		inset: auto 0 0 0;
		width: 100%;
		height: .2em;
		margin: 0;
		min-width: 100%;
		max-width: 100%;

		transition: scale .25s ease-in-out;
		transform-origin: right center;
		scale: 0 1;
	}

	&:hover:after{
		transform-origin: left center;
        scale: 1 1;
	}
}

//
// Add line illustration :after
//

@mixin line-under-always(){
	@include line-after();

	&:after{
		position: absolute;
		inset: auto 0 0 0;
		width: 100%;
		height: .2em;
		margin: 0;
		min-width: 100%;
		max-width: 100%;

		transition: scale .25s ease-in-out;
		transform-origin: right center;
		scale: 1 1;
	}

    &:hover:after{
        animation: underline-animation-in .4s ease-in-out both;
        scale: 1 1;
    }

    @keyframes underline-animation-in{
        0%{
            transform-origin: center right;
            scale: 1 1;
        }
        55%{
            transform-origin: center right;
            scale: 0 1;
        }
        56%{
            transform-origin: center left;
        }
        100%{
            transform-origin: center left;
            scale: 1 1;
        }
    }
}

