//
// Primary button style
//

@mixin primary-button-arrow-style{
	@include arrow-right-small-before();
	@include arrow-right-small-after();

	&::after{
		width: 0;
		translate: -1em 0;
	}

	&::before, &::after{
		transition:
			width $ease-circ .1s,
			scale $ease-bounce .75s,
			translate $ease-bounce .5s;
	}
}

@mixin button-hover{
	transition: scale .25s $ease-bounce;

	&:hover{
		filter: brightness(1.05);
		scale: 1.025 1;
	}
}

@mixin primary-button-arrow-hover{
	&:hover{
		filter: brightness(1.05);
		scale: 1.025 1;

		> *::before{
			width: 0;
			translate: 1em 0;
		}
		> *::after{
			width: 1em;
			translate: 0 0;
		}
	}
}

