.is-section-hero__case-study .work-award-badge {
  position: fixed;
  inset: auto 1% 1% auto;
  z-index: 10;
}

.is-pattern-scroll-vertical {
  position: relative;
}
.is-pattern-scroll-vertical__arrow-line {
  flex: 1;
  height: 100%;
}
.is-pattern-scroll-vertical__arrow-line img {
  min-height: 100%;
  max-width: 3px;
  object-fit: cover;
}
.is-pattern-scroll-vertical__arrow-head {
  position: absolute;
  inset: auto 0 0 0;
  width: 100%;
}
.is-pattern-scroll-vertical__arrow-head img {
  margin: auto;
}

.is-pattern-two-column-illustrations .wp-block-button a {
  font-weight: 900;
  font-style: italic;
}

.is-section-approach-scroll .has-base-background-color {
  border-radius: var(--wp--custom--radius-medium) !important;
}

.is-section-approach-scroll__card-sorting .is-section-approach-scroll__phase-card {
  position: sticky;
  top: 50%;
  transform: translateY(50%);
  max-width: 460px;
  aspect-ratio: 3/4;
  filter: drop-shadow(10px 10px 50px rgba(0, 0, 0, 0.1254901961));
  border-radius: var(--wp--custom--radius-medium);
}
.is-section-approach-scroll__card-sorting .is-section-approach-scroll__right-column {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.is-section-approach-scroll__card-sorting .is-section-approach-scroll__right-column .is-section-approach-scroll__phase-card:nth-child(1) {
  transform: rotate(-3deg);
}
.is-section-approach-scroll__card-sorting .is-section-approach-scroll__right-column .is-section-approach-scroll__phase-card:nth-child(2) {
  transform: rotate(-2deg);
}
.is-section-approach-scroll__card-sorting .is-section-approach-scroll__right-column .is-section-approach-scroll__phase-card:nth-child(3) {
  transform: rotate(-1deg);
}
.is-section-approach-scroll__card-sorting .is-section-approach-scroll__right-column .is-section-approach-scroll__phase-card:nth-child(4) {
  transform: rotate(1deg);
}
.is-section-approach-scroll__card-sorting .is-section-approach-scroll__right-column .is-section-approach-scroll__phase-card:nth-child(5) {
  transform: rotate(-3deg);
}

.is-section-approach-main .approach-phase__content-wrapper {
  min-height: 80svh;
}
@media (min-width: 782px) {
  .is-section-approach-main .approach-phase__content-text {
    padding: var(--wp--preset--spacing--section) 0;
  }
}
.is-section-approach-main .approach-phase__content-text .replaced-svg {
  animation: rotate-small 1s infinite alternate-reverse steps(4);
}
.is-section-approach-main .approach-phase__content-imagery {
  position: relative;
  display: grid;
  grid-template: "container" 1fr;
  grid-area: imagery;
  place-items: center;
  place-content: center;
  overflow: visible;
}
@media (min-width: 782px) {
  .is-section-approach-main .approach-phase__content-imagery {
    min-height: 100%;
  }
}
.is-section-approach-main .approach-phase__dotted-line {
  grid-area: container;
  position: absolute;
  pointer-events: none;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.is-section-approach-main .approach-phase__dotted-line img {
  width: 100%;
  height: 100%;
  object-fit: fill !important;
}
@media (max-width: 782px) {
  .is-section-approach-main .approach-phase__dotted-line {
    height: 100vw;
    rotate: -90deg;
  }
}
.is-section-approach-main .approach-phase__icon {
  grid-area: container;
}
.is-section-approach-main .approach-phase__icon img {
  width: 30vmin;
}
.is-section-approach-main .approach-phase__benefit .wp-block-image {
  margin-left: -2%;
}

.is-section-get-started {
  position: relative;
}
.is-section-get-started__illustration {
  position: absolute !important;
  inset: -10% 15% auto auto;
}
.is-section-get-started .wp-block-site-logo {
  margin-left: calc(var(--wp--preset--spacing--small) * -1);
}

.is-section-solutions-overview__wrapper {
  flex-wrap: wrap;
}
@media (min-width: 900px) {
  .is-section-solutions-overview__wrapper {
    align-items: center;
  }
}
.is-section-solutions-overview__card {
  flex-wrap: wrap;
  flex: 1 1 250px;
}
.is-section-solutions-overview__card.featured {
  order: -1;
  flex: 1 0 100%;
}
@media (min-width: 900px) {
  .is-section-solutions-overview__card.featured {
    order: unset;
    flex: 1 1 250px;
  }
  .is-section-solutions-overview__card.featured .is-section-solutions-overview__illustration {
    width: 60%;
    margin: auto;
  }
  .is-section-solutions-overview__card.featured .is-section-solutions-overview__illustration img {
    width: 100%;
  }
}
.is-section-solutions-overview__illustration {
  width: 40%;
}
.is-section-solutions-overview p {
  text-wrap-style: balance;
}

.is-section-brand-explained__your-brand {
  border-radius: var(--wp--custom--radius-large);
}
.is-section-brand-explained__brand-identity {
  border-radius: var(--wp--custom--radius-medium);
}
.is-section-brand-explained__visual-identity, .is-section-brand-explained__verbal-identity {
  border-radius: var(--wp--custom--radius-small);
}
.is-section-brand-explained__icon {
  margin-block-start: -10% !important;
  background-color: var(--wp--preset--color--contrast);
  padding: var(--wp--preset--spacing--large);
  border-radius: 100%;
  aspect-ratio: 1/1;
}
.is-section-brand-explained h5 {
  margin-block-start: 0;
}
.is-section-brand-explained ul.wp-block-list.is-style-tags li {
  border: solid currentColor;
  background-color: unset;
  text-align: center;
  flex: 1 1 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.is-pattern-case-study-section__content-wrapper {
  padding-top: var(--wp--preset--spacing--section);
  padding-bottom: var(--wp--preset--spacing--section);
}

.rpi {
  font-family: var(--wp--preset--font-family--primary);
}

.rpi-content .rpi-url {
  margin-top: var(--wp--preset--spacing--x-large) !important;
  border-radius: var(--wp--custom--radius-small);
  padding: var(--wp--preset--spacing--small) var(--wp--preset--spacing--medium) !important;
  background-color: var(--wp--preset--color--primary) !important;
  color: var(--wp--preset--color--base) !important;
  transition: scale 0.25s var(--wp--custom--ease-bounce);
}
.rpi-content .rpi-url:hover {
  filter: brightness(1.05);
  scale: 1.025 1;
}

.rpi-cards {
  display: block;
  column-width: 300px;
}

.rpi-content .rpi-card {
  display: inline-block;
  margin-bottom: var(--wp--preset--spacing--large);
}

.rpi-text {
  min-height: fit-content;
  font-style: italic;
}

.rpi-slider-btn {
  display: none;
  visibility: hidden;
}

a.rpi-name {
  color: var(--wp--preset--color--contrast) !important;
}

.rpi .rpi-badge-left .rpi-star::after {
  background-color: var(--wp--preset--color--primary);
}

.work-award-badge {
  width: clamp(100px, 20%, 150px);
  background-color: var(--wp--preset--color--base);
  border-radius: 100%;
  aspect-ratio: 1/1;
  z-index: 1;
  margin: 0;
  animation: rotate-small 1s infinite alternate-reverse steps(4);
  transition: scale 0.5s var(--wp--custom--ease-bounce);
}
.work-award-badge:hover {
  scale: 1.1;
}

body.single-post .entry-content .wp-block-separator {
  color: var(--wp--preset--color--tertiary);
  margin-top: var(--wp--preset--spacing--xx-large);
  margin-bottom: var(--wp--preset--spacing--xx-large);
}
body.single-post .entry-content > .wp-block-heading {
  margin-top: var(--wp--preset--spacing--x-large);
}